<script>

  export default {
    name: 'Institution',
    data() {
      return {
        signUpEmail: '',
        loginEmail: '',
        loginPassword: '',
        signUpConfirmPassword: '',
        signUpPassword: '',
        isInstitutionAccountCreatedVisible: false,
        isErrorVisible: false
      }
    },
    computed: {
      passwordConfirmationRules() {
        return [
          () => (this.signUpConfirmPassword === this.signUpPassword) || "Passwords must match"
        ];
      }
    },
    methods: {
      signUp() {

        var request = {
          username: this.signUpEmail,
          password: this.signUpPassword
        };

        this.$store.dispatch('register', request)
                .then(() => {
                          this.signUpConfirmPassword = '';
                          this.signUpPassword = '';
                          this.signUpEmail = '';
                          this.isInstitutionAccountCreatedVisible = true;
                  setTimeout( () => {
                    this.isInstitutionAccountCreatedVisible = false
                  }, 3000)
                        }
                ).catch(err => console.log(err))

      },
      logIn : async function(){

        var request = {
          username: this.loginEmail,
          password: this.loginPassword
        };

        this.$store.dispatch('login', request)
                .then(() => this.$router.push('/institutionloggedin'))
                .catch(err => {
                  console.log(err);
                  this.loginEmail = '';
                  this.loginPassword = '';
                  this.isErrorVisible = true;
                  setTimeout( () => {
                    this.isErrorVisible = false
                  }, 5000)
                });

      }
    }
  }
</script>
